import * as THREE from 'three'
import {GLTFLoader} from './loaders/GLTFLoader'
import {RGBELoader} from './loaders/RGBELoader'
import {RoughnessMipmapper} from './utils/RoughnessMipmapper'
import OrbitControls from './controllers/OrbitControls'
import Stats from 'stats.js'
import {get as getGlobal} from "./utils/globalData";
import * as dat from 'dat.gui';
import PubSub from 'pubsub-js'
import PlatformScene from './sceneConfig/PlatformScene'
import {addClipActionFolder} from './utils/util'
import {TriggerType} from "./sceneConfig/action/BaseAction";
import {Vector3} from "three";
import {Geometry} from "three";
import DeviceScene from "./sceneConfig/DeviceScene";
import {error} from "three";

const TWEEN = require('@tweenjs/tween.js');

class SceneManager {

    constructor() {
        this.container = null;
        this.scene = null;
        this.camera = null;
        this.renderer = null;
        this.stats = null;
        this.pmremGenerator = null;
        this.mixer = null;
        this.controls = null;
        this.width = 400;
        this.height = 300;
        this.clock = new THREE.Clock();
        this.raycaster = null;
        this.ifInit = false;
        this.isLoaded = false;
        this.bufferPool = [];
        this.currentGltf = undefined;
        this.group = new THREE.Group();
        this.gltfs = [];
        this.tween = null;
        this.sceneModel = undefined;
        // eslint-disable-next-line no-lone-blocks
        {
            this.defaultPositionOfCamera = new Vector3(-1, 3, 5);
            // this.defaultUpOfCamera = new Vector3(0, 1, 0);
            this.defaultLookAtOfCamera = new Vector3(0, 0, 0);

            this.defaultPositionOfModel = new Vector3(0, 0, 0);
            this.defaultRotationOfModel = new Vector3(0, 0, 0);
            this.defaultScaleOfModel = new Vector3(1, 1, 1);
        }
    }

    loadModelWithBg = (bgPath, bgFileName, modelPath, modelName) => {

        // todo : loading

        this.group.children.forEach(item => item.visible = false);

        if (this.bufferPool.length === 0) {

            let loader = new GLTFLoader().setPath(modelPath);
            loader.load(modelName, (gltf) => {
                this.currentGltf = gltf;
                let model = gltf.scene;
                model.position.set(this.defaultPositionOfModel.x, this.defaultPositionOfModel.y, this.defaultPositionOfModel.z);
                model.rotation.setFromVector3(new Vector3(this.defaultRotationOfModel.x, this.defaultRotationOfModel.y, this.defaultRotationOfModel.z));
                model.scale.set(this.defaultScaleOfModel.x, this.defaultScaleOfModel.y, this.defaultScaleOfModel.z);

                this.setPMREM();
                this.setTextureTo(model);

                model.name = this.bufferPool.length;

                //
                let obj = {modelPath: modelPath, modelName: modelName, model: gltf};
                this.bufferPool.push(obj);

                this.mixer = new THREE.AnimationMixer(model);
                gltf.animations.forEach(anamationClip => {
                    this.mixer.clipAction(anamationClip).stop();
                });

                this.group.add(model);

                //adjust control
                this.adjustControl();

                this.render();
                this.isLoaded = true;
                PubSub.publish(SceneManager.path + 'loaded', {modelName: modelName});
                // this.enableControls();
            });

            return;
        }

        for (let i = 0; i < this.bufferPool.length; i++) {
            if (this.bufferPool[i].modelPath === modelPath && this.bufferPool[i].modelName === modelName) {
                this.currentGltf = this.bufferPool[i].model;
                this.group.children[i].visible = true;
                this.mixer = new THREE.AnimationMixer(this.bufferPool[i].model.scene);
                this.bufferPool[i].model.animations.forEach(anamationClip => {
                    this.mixer.clipAction(anamationClip).stop();
                });
                this.isLoaded = true;

                //adjust control
                this.adjustControl();

                PubSub.publish(SceneManager.path + 'loaded', {modelName: modelName});

                this.render();
                // PubSub.publish(SceneManager.path + 'loadedFromCache',{modelName:modelName});
                // this.enableControls(this.mixer,this.bufferPool[i].model);
                return;
            }
        }

        // let roughnessMipmapper = new RoughnessMipmapper(this.renderer);
        let loader = new GLTFLoader().setPath(modelPath);
        loader.load(modelName, (gltf) => {
            this.currentGltf = gltf;
            let model = gltf.scene;
            model.position.set(this.defaultPositionOfModel.x, this.defaultPositionOfModel.y, this.defaultPositionOfModel.z);
            model.rotation.setFromVector3(new Vector3(this.defaultRotationOfModel.x, this.defaultRotationOfModel.y, this.defaultRotationOfModel.z));
            model.scale.set(this.defaultScaleOfModel.x, this.defaultScaleOfModel.y, this.defaultScaleOfModel.z);

            this.setPMREM();

            this.setTextureTo(model);

            //
            model.name = this.bufferPool.length;
            let obj = {modelPath: modelPath, modelName: modelName, model: gltf};
            this.bufferPool.push(obj);

            this.group.add(model);

            this.mixer = new THREE.AnimationMixer(model);
            gltf.animations.forEach(anamationClip => {
                this.mixer.clipAction(anamationClip).stop();
            });

            //adjust control
            this.adjustControl();

            this.render();
            this.isLoaded = true;
            PubSub.publish(SceneManager.path + 'loaded', {modelName: modelName});

        });


    };

    pick = (offset, x, y) => {

        let mouse = new THREE.Vector2();
        mouse.x = ((x - offset.x) / this.width) * 2 - 1;
        mouse.y = -((y - offset.y) / this.height) * 2 + 1;
        console.log(offset.x + ' ' + offset.y + ' ' + mouse.x + ' ' + mouse.y);
        let origin = this.camera.position;

        let vector = new THREE.Vector3(mouse.x, mouse.y, 0.5).unproject(this.camera);
        let direction = vector.sub(this.camera.position).normalize();

        let raycaster = new THREE.Raycaster(origin, direction);
        // calculate objects intersecting the picking ray
        let intersects = raycaster.intersectObjects(this.scene.children, true);
        if (intersects.length > 0) {
            PubSub.publish(SceneManager.path + 'pick', {name: intersects[0].object.name, type: TriggerType.Single});
            return intersects[0];
        } else {
            return null;
        }
    };

    animate = (time) => {

        TWEEN.default.update(time);

        if (this.ifInit) {
            let delta = this.clock.getDelta();
            this.mixer.update(delta);
            this.controls.update(delta);
            this.render();
            this.stats.update();

            if (this.mixer && this.animationControls.length > 0) {
                this.mixer.update(delta);
                this.animationControls.forEach((value, index) => {
                    value.time = this.mixer.time;
                    value.effectiveTimeScale = this.clipAndActions[index].action.getEffectiveTimeScale();
                    value.effectiveWeight = this.clipAndActions[index].action.getEffectiveWeight();
                });

            }

            PubSub.publish(SceneManager.path + 'animate', {delta: delta, isLoaded: this.isLoaded});
        }

        requestAnimationFrame(this.animate.bind(this));

    };

    // current1 
    // target1 controlstarget
    // current2 
    // target2 controlstarget


    animateCamera = (current1, target1, current2, target2, lookAt) => {
        this.camera.lookAt(lookAt);
        let self = this;
        let positionVar = {
            x1: current1.x,
            y1: current1.y,
            z1: current1.z,
            x2: target1.x,
            y2: target1.y,
            z2: target1.z
        };

        this.controls.enabled = false;
        this.tween = new TWEEN.default.Tween(positionVar);

        this.tween.to({
            x1: current2.x,
            y1: current2.y,
            z1: current2.z,
            x2: target2.x,
            y2: target2.y,
            z2: target2.z
        }, 1500);

        this.tween.onUpdate(function () {
            self.camera.position.x = positionVar.x1;
            self.camera.position.y = positionVar.y1;
            self.camera.position.z = positionVar.z1;
            self.controls.target.x = positionVar.x2;
            self.controls.target.y = positionVar.y2;
            self.controls.target.z = positionVar.z2;
            self.controls.update();
            console.log(positionVar);
        });

        this.tween.onComplete(function () {
            ///
            self.controls.enabled = true;
        })

        this.tween.easing(TWEEN.default.Easing.Cubic.InOut);
        this.tween.start();
    }

    render = () => {
        this.renderer.render(this.scene, this.camera);
    };

    onWindowResize = () => {

        this.camera.aspect = this.container.offsetWidth / this.container.offsetHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(this.width, this.height);
    };

    init = () => {
        this.raycaster = new THREE.Raycaster();
        this.scene = new THREE.Scene();
        this.scene.add(this.group);

        this.camera = new THREE.PerspectiveCamera(60, this.width / this.height, 1, 2000);
        this.camera.position.x = this.defaultPositionOfCamera.x;
        this.camera.position.y = this.defaultPositionOfCamera.y;
        this.camera.position.z = this.defaultPositionOfCamera.z;
        // this.camera.lookAt( this.scene.position - new THREE.Vector3(0,0,0) );
        // this.camera.lookAt( this.scene.position);
        this.camera.lookAt(this.defaultLookAtOfCamera);

        this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true});
        this.pmremGenerator = new THREE.PMREMGenerator(this.renderer);
        this.pmremGenerator.compileEquirectangularShader();
        this.renderer.setClearColor(0xEEEEEE, 0.0);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(this.width, this.height);
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 0.8;
        this.renderer.outputEncoding = THREE.sRGBEncoding;

        let ambi = new THREE.AmbientLight(0x686868); //
        this.scene.add(ambi);
        let spotLight = new THREE.DirectionalLight(0xffffff);  //
        spotLight.position.set(550, 100, 550);
        spotLight.intensity = 0.6;
        this.scene.add(spotLight);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        this.controls.addEventListener('change', this.render);
        this.controls.minDistance = 0.1;
        this.controls.maxDistance = 13;
        // this.controls.autoRotate = false;
        // this.controls.autoRotateSpeed = -10;
        this.controls.screenSpacePanning = true;
        this.controls.target.set(0, 0, -0.2);


        this.stats = new Stats();
        this.animate();

    };

    appendChild = (div) => {

        let newContainer = document.getElementById(div);
        if (!newContainer)
            return;

        if (newContainer !== this.container) {
            if (this.container !== null) {
                if (this.stats.dom in this.container.children) {
                    this.container.removeChild(this.stats.dom);
                }
                if (this.renderer.domElement in this.container.children) {
                    this.container.removeChild(this.renderer.domElement)
                }
                window.removeEventListener("resize", this.onWindowResize, false);
                this.ifInit = false;
            }
            this.container = newContainer;
            this.width = newContainer.offsetWidth;
            this.height = newContainer.offsetHeight;
            console.log(this.width, this.height);
            this.camera.aspect = this.width / this.height;
            this.renderer.setSize(this.width, this.height);
            this.container.appendChild(this.stats.dom);
            this.mixer = new THREE.AnimationMixer();
            this.container.appendChild(this.renderer.domElement);

            this.ifInit = true;


        }


        window.addEventListener('resize', this.onWindowResize, false);

    };

    getAnimationClipAndActionBy = (name) => {
        let animationClip = undefined;
        let animationAction = undefined;
        let clip = THREE.AnimationClip.findByName(this.currentGltf.animations, name);
        if (clip === null) return true;
        let action = this.mixer.clipAction(clip);
        if (action === null) return true;
        animationClip = clip;
        animationAction = action;
        return {clip: animationClip, action: animationAction};
    };

    setCameraPos(position, up, lookat) {

        if (position !== null) {
            this.camera.position.x = position.x;
            this.camera.position.y = position.y;
            this.camera.position.z = position.z;
        } else {
            console.log('position is null')
        }

        if (lookat !== null) {
            this.camera.lookAt(lookat);
        } else {
            console.log('lookat is null')
        }
        this.camera.lookAt(new Vector3());

        // if (up !== null) {
        //     this.camera.up = up;
        // } else {
        //     console.log('up is null')
        // }
    }

    setModelPos(position, rotation, scale) {

        if (position !== null) {
            this.defaultPositionOfModel.set(position.x, position.y, position.z);
        } else {
            console.log('position is null')
        }

        if (rotation !== null) {
            this.defaultRotationOfModel.set(rotation.x, rotation.y, rotation.z);
        } else {
            console.log('rotation is null')
        }

        if (scale !== null) {
            this.defaultScaleOfModel.set(scale.x, scale.y, scale.z);
        } else {
            console.log('scale is null')
        }

        if (this.currentGltf !== undefined) {
            this.currentGltf.scene.position.set(this.defaultPositionOfModel.x, this.defaultPositionOfModel.y, this.defaultPositionOfModel.z);
            this.currentGltf.scene.rotation.setFromVector3(new Vector3(this.defaultRotationOfModel.x, this.defaultRotationOfModel.y, this.defaultRotationOfModel.z));
            this.currentGltf.scene.scale.set(this.defaultScaleOfModel.x, this.defaultScaleOfModel.y, this.defaultScaleOfModel.z);
        }


    }

    // adjust control  camera near and camera far to the current model
    adjustControl() {

        this.setCameraPos(this.sceneModel.positionOfCamera,this.sceneModel.upOfCamera,this.sceneModel.lookatOfCamera);
        this.setModelPos(this.sceneModel.positionOfModel,this.sceneModel.rotationOfModel,this.sceneModel.scaleOfModel);

        let object = this.currentGltf.scene;
        const box = new THREE.Box3().setFromObject(object);
        const size = box.getSize(new Vector3()).length();
        // const center = box.getCenter(new Vector3());
        this.controls.reset();
        // object.position.x += (object.position.x - center.x);
        // object.position.y += (object.position.y - center.y);
        // object.position.z += (object.position.z - center.z);
        this.controls.maxDistance = size * 10;
        this.camera.near = size / 100;
        this.camera.far = size * 100;
        this.camera.updateProjectionMatrix();
        this.controls.saveState();
    }

    //set PMREM
    setPMREM() {
        if (SceneManager.path === DeviceScene.path) {
            this.getPmremTexture().then(({envMap}) => {
                this.scene.environment = envMap;
            });
        }
    }

    getPmremTexture() {
        let path = '/source/textures/equirectangular/venice_sunset_1k.hdr';//L1024.png
        // no envmap
        if (!path) return Promise.resolve({envMap: null});
        return new Promise((resolve, reject) => {

            new RGBELoader()
                .setDataType(THREE.UnsignedByteType)
                .load(path, (texture) => {

                    const envMap = this.pmremGenerator.fromEquirectangular(texture).texture;
                    this.pmremGenerator.dispose();

                    resolve({envMap});

                }, undefined, reject);

        });
    }

    // 
    // 
    // 
    // 
    setTextureTo(model) {

        if (SceneManager.path === PlatformScene.path) {

            let roughnessMipmapper = new RoughnessMipmapper(this.renderer);
            let textureLoader = new THREE.TextureLoader();

            let lightMap = null;
            let path = this.sceneModel.lightMapPath();
            if (typeof (path) === 'string' && path.length > 0) {
                lightMap = textureLoader.load(path);
            }

            model.traverse((child) => {
                    if (child instanceof THREE.Mesh && child.isMesh) {
                        roughnessMipmapper.generateMipmaps(child.material);
                        child.material.lightMap = lightMap;
                        child.material.needsUpdate = true;
                    }
                }
            );

            roughnessMipmapper.dispose();

            return;

        }

        // device
        let roughnessMipmapper = new RoughnessMipmapper(this.renderer);
        let textureLoader = new THREE.TextureLoader();

        let lightMap = null;
        let path = this.sceneModel.lightMapPath();
        if (typeof (path) === 'string' && path.length > 0) {
            lightMap = textureLoader.load(path);
        }

        let colorMap = null;
        path = this.sceneModel.colorMapFilePath();
        if (typeof (path) === 'string' && path.length > 0) {
            colorMap = textureLoader.load(path);
        }

        let metallicMap = null;
        path = this.sceneModel.metallicMapFilePath();
        if (typeof (path) === 'string' && path.length > 0) {
            metallicMap = textureLoader.load(path);
        }

        let roughnessMap = null;
        path = this.sceneModel.roughnessMapFilePath();
        if (typeof (path) === 'string' && path.length > 0) {
            roughnessMap = textureLoader.load(path);
        }

        model.traverse((child) => {
            if (child instanceof THREE.Mesh && child.isMesh) {
                // set
                roughnessMipmapper.generateMipmaps(child.material);
                if (SceneManager.path === DeviceScene.path && child.name === 'Frame') {
                    // error
                    // child.material.emissive = new THREE.Color(0xFF0000);
                    // child.material.transparent = true;
                    // child.material.opacity = 1;
                    // child.material.color.setHex(0xFF0000);

                    // 
                    // child.material.map = colorMap;
                    // child.material.roughnessMap = roughnessMap;
                    // child.material.metalnessMap = metallicMap;
                } else {
                    child.material.lightMap = lightMap;
                }
                child.material.needsUpdate = true;
            }
        });
        roughnessMipmapper.dispose();
    }

    // animation test
    animationControls = [];
    clipAndActions = [];
    mixerControls = {
        time: 0,
        timeScale: 1,
        stopAllAction: function () {
            const sceneManager = getGlobal('sceneManager');
            sceneManager.mixer.stopAllAction()
        },
    };

    enableControls(mixer, gltf) {

        if (SceneManager.path !== PlatformScene.path) {
            return;
        }
        let _this = this;
        let gui = new dat.GUI();
        let mixerFolder = gui.addFolder("AnimationMixer");
        mixerFolder.add(this.mixerControls, "time").listen();
        mixerFolder.add(this.mixerControls, "timeScale", 0, 5).onChange(function (timeScale) {
            _this.mixer.timeScale = timeScale;
        });
        mixerFolder.add(this.mixerControls, "stopAllAction").listen();

        let clipAndAction = null;
        let control = null;

        clipAndAction = SceneManager.getAnimationClipAndActionByNameWithMixerAndGltfs('Half_N', [gltf], mixer);
        control = addClipActionFolder("Half_N", gui, clipAndAction.action, clipAndAction.clip);
        this.animationControls.push(control);
        this.clipAndActions.push(clipAndAction);

        clipAndAction = SceneManager.getAnimationClipAndActionByNameWithMixerAndGltfs('Half_S', [gltf], mixer);
        control = addClipActionFolder("Half_S", gui, clipAndAction.action, clipAndAction.clip);
        this.animationControls.push(control);
        this.clipAndActions.push(clipAndAction);

    }

}

SceneManager.path = PlatformScene.path;

SceneManager.getAnimationClipAndActionByNameWithMixerAndGltfs = (name, gltfs, mixer) => {
    let animationClip = undefined;
    let animationAction = undefined;
    gltfs.forEach((value, index) => {
        let clip = THREE.AnimationClip.findByName(value.animations, name);
        if (clip === null) return true;
        let action = mixer.clipAction(clip);
        if (action === null) return true;
        animationClip = clip;
        animationAction = action;
    });
    return {clip: animationClip, action: animationAction};
};

export default SceneManager


