import React,{PureComponent} from 'react'
import Status from '../../components/status/status'
import NetworkError from '../../components/networkError/networkError'
import './device.less'
import '../../img/map.png'
import http from '../../utils/http'
import {get as getGlobal} from "../../common/utils/globalData";
import {Vector2} from "three";
import SceneManager from "../../common/SceneManager";
import DeviceScene from "../../common/sceneConfig/DeviceScene";



class Device extends PureComponent{

    state={
        statusList:[],
        showError:false
    }

    handleShow=()=>{
        this.setState({showError:true})
        setTimeout(()=>{
            this.setState({showError:false})
        },2000)
    }

    handlePick=(e)=>{
        const sceneManager = getGlobal('sceneManager');
        const node = sceneManager.pick(new Vector2(200, 0), e.clientX, e.clientY);
        if(node!==null){

            sceneManager.animateCamera(sceneManager.camera.position, sceneManager.controls.position0,{x:-1,y:2,z:5},{x:0,y:0,z:0},{x:0,y:0,z:0})
        }else {
            sceneManager.animateCamera(sceneManager.camera.position, sceneManager.controls.position0,{x:0,y:5,z:15},{x:0,y:0,z:0},{x:0,y:0,z:0})
        }
        // console.log(node);
    }

    getStatus=()=>{
        http.get('/virtualData/status.json')
            .then(res=>{
                this.setState({statusList:res.data.data || []})
                console.log(res.data.data)
            })
    };

    componentWillMount() {
        this.getStatus();
    }

    componentDidMount() {
        const _this = this;
        setTimeout(()=>{
            const sceneManager = getGlobal('sceneManager');
            const application = getGlobal('Application');
            let scene = application.deviceTVM;
            SceneManager.path = DeviceScene.path;
            sceneManager.sceneModel = scene;
            sceneManager.appendChild('model');
            sceneManager.loadModelWithBg(scene.bgFilePath(), scene.bgFileName(), scene.modelFilePath(), scene.modelFileName());
            sceneManager.container.addEventListener('disclick',_this.handlePick,false);
            // sceneManager.setCameraPos(scene.positionOfCamera,scene.upOfCamera,scene.lookatOfCamera);
            // sceneManager.setModelPos(scene.positionOfModel,scene.rotationOfModel,scene.scaleOfModel);
        },10)

    }

    componentWillUnmount() {
        clearTimeout();
    }

    render() {
        const { statusList,showError } = this.state;
        // console.log(statusList);
        return(
            <div className='device'>
                <div className='left'>
                    <div id='model'>
                        {
                            showError?<NetworkError/>:""
                        }
                    </div>
                    <div className='repair-btn' onClick={this.handleShow}>
                        <div className='icon'></div>
                        <div className='text'></div>
                    </div>
                </div>
                <div className='right'>
                    <div className='position'>
                        <img className='icon' src={[require('../../img/map.png')]} />
                        <div className='title'></div>
                        <div className='desc'>A3</div>
                    </div>
                    <Status list={statusList}/>
                </div>
            </div>
        )
    }
}

export default Device;
