import React,{Component} from 'react'
import Example from '../pages/example/device'
import Device from '../pages/device/device'
import Platform from '../pages/platform/Platform'
import SceneDemo from '../pages/sceneManagerDemo/sceneDemo'
import {Layout, Menu} from 'antd'
import {Link,Route} from 'react-router-dom'
const {  Content, Sider } = Layout;
const { SubMenu } = Menu;

class OwnLayout extends Component{


    state = {
        collapsed: false,
    };

    onCollapse = collapsed => {
        this.setState({ collapsed });
    };


    render() {
        return(
            <Layout style={{ minHeight: '100vh' }}>
                <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
                    <Menu theme="dark" defaultSelectedKeys={['platform']} mode="inline">
                        <Menu.Item key='platform'><Link to={{pathname:'/',state:{id:0}}}></Link></Menu.Item>
                        <SubMenu
                            key="device"
                            title={<span></span>}
                        >
                            <SubMenu
                                key='TVM'
                                title={<span>TVM</span>}
                            >
                                <Menu.Item key='TVM-1'><Link to={{pathname:'/example',state:{id:1}}}>1</Link></Menu.Item>
                                <Menu.Item key='TVM-2'><Link to={{pathname:'/sceneManager',state:{id:2}}}>2</Link></Menu.Item>
                                <Menu.Item key='TVM-3'><Link to={{pathname:'/example',state:{id:3}}}>3</Link></Menu.Item>
                                <Menu.Item key='TVM-4'><Link to={{pathname:'/example',state:{id:4}}}>4</Link></Menu.Item>
                            </SubMenu>
                            <SubMenu
                                key='AGM'
                                title={<span>AGM</span>}
                            >
                                <Menu.Item key='AGM-1'><Link to={{pathname:'/device',state:{id:1}}}>1</Link></Menu.Item>
                            </SubMenu>
                            <SubMenu
                                key='CCTV'
                                title={<span>CCTV</span>}
                            >

                            </SubMenu>
                        </SubMenu>
                    </Menu>
                </Sider>
                <Layout>
                    <Content>
                        <Route exact path='/' component={Platform} />
                        <Route exact path='/example' component={Example} />
                        <Route exact path='/device' component={Device} />
                        <Route exact path='/sceneManager' component={SceneDemo}/>

                    </Content>
                </Layout>
            </Layout>
        )
    }
}

export default OwnLayout;
